/** @type {import('../types.d.ts').LanguageProto<'stylus'>} */
export default {
	id: 'stylus',
	grammar () {
		const unit = {
			pattern: /(\b\d+)(?:%|[a-z]+)/,
			lookbehind: true,
		};
		// 123 -123 .123 -.123 12.3 -12.3
		const number = {
			pattern: /(^|[^\w.-])-?(?:\d+(?:\.\d+)?|\.\d+)/,
			lookbehind: true,
		};

		const inside = /** @type {Grammar} */ ({
			'comment': {
				pattern: /(^|[^\\])(?:\/\*[\s\S]*?\*\/|\/\/.*)/,
				lookbehind: true,
			},
			'url': {
				pattern: /\burl\((["']?).*?\1\)/i,
				greedy: true,
			},
			'string': {
				pattern: /("|')(?:(?!\1)[^\\\r\n]|\\(?:\r\n|[\s\S]))*\1/,
				greedy: true,
			},
			'interpolation': {
				pattern: /\{[^\r\n}:]+\}/,
				alias: 'variable',
				inside: {
					'delimiter': {
						pattern: /^\{|\}$/,
						alias: 'punctuation',
					},
					$rest: null,
				},
			},
			'func': {
				pattern: /[\w-]+\([^)]*\).*/,
				inside: {
					'function': /^[^(]+/,
					$rest: null,
				},
			},
			'important': /\B!(?:important|optional)\b/i,
			'keyword': {
				pattern: /(^|\s+)(?:(?:else|for|if|return|unless)(?=\s|$)|@[\w-]+)/,
				lookbehind: true,
			},
			'hexcode': /#[\da-f]{3,6}/i,
			'color': [
				/\b(?:AliceBlue|AntiqueWhite|Aqua|Aquamarine|Azure|Beige|Bisque|Black|BlanchedAlmond|Blue|BlueViolet|Brown|BurlyWood|CadetBlue|Chartreuse|Chocolate|Coral|CornflowerBlue|Cornsilk|Crimson|Cyan|DarkBlue|DarkCyan|DarkGoldenRod|DarkGr[ae]y|DarkGreen|DarkKhaki|DarkMagenta|DarkOliveGreen|DarkOrange|DarkOrchid|DarkRed|DarkSalmon|DarkSeaGreen|DarkSlateBlue|DarkSlateGr[ae]y|DarkTurquoise|DarkViolet|DeepPink|DeepSkyBlue|DimGr[ae]y|DodgerBlue|FireBrick|FloralWhite|ForestGreen|Fuchsia|Gainsboro|GhostWhite|Gold|GoldenRod|Gr[ae]y|Green|GreenYellow|HoneyDew|HotPink|IndianRed|Indigo|Ivory|Khaki|Lavender|LavenderBlush|LawnGreen|LemonChiffon|LightBlue|LightCoral|LightCyan|LightGoldenRodYellow|LightGr[ae]y|LightGreen|LightPink|LightSalmon|LightSeaGreen|LightSkyBlue|LightSlateGr[ae]y|LightSteelBlue|LightYellow|Lime|LimeGreen|Linen|Magenta|Maroon|MediumAquaMarine|MediumBlue|MediumOrchid|MediumPurple|MediumSeaGreen|MediumSlateBlue|MediumSpringGreen|MediumTurquoise|MediumVioletRed|MidnightBlue|MintCream|MistyRose|Moccasin|NavajoWhite|Navy|OldLace|Olive|OliveDrab|Orange|OrangeRed|Orchid|PaleGoldenRod|PaleGreen|PaleTurquoise|PaleVioletRed|PapayaWhip|PeachPuff|Peru|Pink|Plum|PowderBlue|Purple|Red|RosyBrown|RoyalBlue|SaddleBrown|Salmon|SandyBrown|SeaGreen|SeaShell|Sienna|Silver|SkyBlue|SlateBlue|SlateGr[ae]y|Snow|SpringGreen|SteelBlue|Tan|Teal|Thistle|Tomato|Transparent|Turquoise|Violet|Wheat|White|WhiteSmoke|Yellow|YellowGreen)\b/i,
				{
					pattern:
						/\b(?:hsl|rgb)\(\s*\d{1,3}\s*,\s*\d{1,3}%?\s*,\s*\d{1,3}%?\s*\)\B|\b(?:hsl|rgb)a\(\s*\d{1,3}\s*,\s*\d{1,3}%?\s*,\s*\d{1,3}%?\s*,\s*(?:0|0?\.\d+|1)\s*\)\B/i,
					inside: {
						'unit': unit,
						'number': number,
						'function': /[\w-]+(?=\()/,
						'punctuation': /[(),]/,
					},
				},
			],
			'entity': /\\[\da-f]{1,8}/i,
			'unit': unit,
			'boolean': /\b(?:false|true)\b/,
			'operator': [
				// We want non-word chars around "-" because it is
				// accepted in property names.
				/~|[+!\/%<>?=]=?|[-:]=|\*[*=]?|\.{2,3}|&&|\|\||\B-\B|\b(?:and|in|is(?: a| defined| not|nt)?|not|or)\b/,
			],
			'number': number,
			'punctuation': /[{}()\[\];:,]/,
		});

		/** @type {Grammar} */ (
			/** @type {GrammarToken} */ (inside['interpolation']).inside
		).$rest = inside;
		/** @type {Grammar} */ (/** @type {GrammarToken} */ (inside['func']).inside).$rest = inside;

		return /** @type {Grammar} */ ({
			'atrule-declaration': {
				pattern: /(^[ \t]*)@.+/m,
				lookbehind: true,
				inside: {
					'atrule': /^@[\w-]+/,
					$rest: inside,
				},
			},
			'variable-declaration': {
				pattern: /(^[ \t]*)[\w$-]+\s*.?=[ \t]*(?:\{[^{}]*\}|\S.*|$)/m,
				lookbehind: true,
				inside: {
					'variable': /^\S+/,
					$rest: inside,
				},
			},

			'statement': {
				pattern: /(^[ \t]*)(?:else|for|if|return|unless)[ \t].+/m,
				lookbehind: true,
				inside: {
					'keyword': /^\S+/,
					$rest: inside,
				},
			},

			// A property/value pair cannot end with a comma or a brace
			// It cannot have indented content unless it ended with a semicolon
			'property-declaration': {
				pattern:
					/((?:^|\{)([ \t]*))(?:[\w-]|\{[^}\r\n]+\})+(?:\s*:\s*|[ \t]+)(?!\s)[^{\r\n]*(?:;|[^{\r\n,]$(?!(?:\r?\n|\r)(?:\{|\2[ \t])))/m,
				lookbehind: true,
				inside: {
					'property': {
						pattern: /^[^\s:]+/,
						inside: {
							'interpolation': inside.interpolation,
						},
					},
					$rest: inside,
				},
			},

			// A selector can contain parentheses only as part of a pseudo-element
			// It can span multiple lines.
			// It must end with a comma or an accolade or have indented content.
			'selector': {
				pattern:
					/(^[ \t]*)(?:(?=\S)(?:[^{}\r\n:()]|::?[\w-]+(?:\([^)\r\n]*\)|(?![\w-]))|\{[^}\r\n]+\})+)(?:(?:\r?\n|\r)(?:\1(?:(?=\S)(?:[^{}\r\n:()]|::?[\w-]+(?:\([^)\r\n]*\)|(?![\w-]))|\{[^}\r\n]+\})+)))*(?:,$|\{|(?=(?:\r?\n|\r)(?:\{|\1[ \t])))/m,
				lookbehind: true,
				inside: {
					'interpolation': inside.interpolation,
					'comment': inside.comment,
					'punctuation': /[{},]/,
				},
			},

			'func': inside.func,
			'string': inside.string,
			'comment': {
				pattern: /(^|[^\\])(?:\/\*[\s\S]*?\*\/|\/\/.*)/,
				lookbehind: true,
				greedy: true,
			},
			'interpolation': inside.interpolation,
			'punctuation': /[{}()\[\];:.]/,
		});
	},
};

/**
 * @typedef {import('../types.d.ts').Grammar} Grammar
 * @typedef {import('../types.d.ts').GrammarToken} GrammarToken
 */
